body {
  --hover-color: #00acdc;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 14px;
}

a {
  color: #000;
  text-decoration: none;
}

header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

nav ul {
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

nav li {
  margin-left: 20px;
}

nav li:first-child {
  margin: 0;
}

nav a {
  text-transform: uppercase;
  padding: 23px 0;
  display: flex;
  position: relative;
}

nav a:hover, .is-active {
  color: var(--hover-color);
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}
